
import VHeader from '@/components/v-header/v-header'
import axios from '@/api/axiosApi'
import { isIOS } from '@/assets/js/brower.js'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import Scroll from '@/components/scroll/scroll'

export default {
data () {
return {
showMap: false,
menusMap: {},
showCancel: true,
titleMap: '操作提示',
showGaoDei: false,
text: '',
detailServiceIcon: [
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '美食'
},
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '景点'
},
{
pic: require('@/assets/img/details_icon/details-recommend.png'),
value: '酒店'
},
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '停车场'
},
{
pic: require('@/assets/img/details_icon/details-recommend.png'),
value: '加油站'
},
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '卫生间'
},
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '医院'
},
{
pic: require('@/assets/img/details_icon/details-720.png'),
value: '汽车维修'
}
],
detailServiceTextEat: [
'美食',
'小吃',
'火锅',
'咖啡厅',
'肯德基'
],
detailServiceTextLive: [
'酒店',
'宾馆',
'青年旅社',
'星级酒店',
'旅馆',
'主题酒店',
'商务酒店',
'快捷酒店',
'招待所',
'家庭旅馆'
],
detailServiceTextShopping: [
'超市',
'商场',
'步行街',
'五金店',
'便利店',
'书店',
'花店',
'农贸市场'
],
data: []
}
},
components: {
VHeader,
Grid,
GridItem,
Scroll,
Actionsheet,
Confirm
},
methods: {
detailsItemsSearch (content) {
this.text = content
axios.get(this, '/v1/product/' + this.$route.params.productId, null, (data) => {
this.data = data
if (isIOS()) {


window.webkit.messageHandlers.NativeMethod.postMessage({'methodName': 'openMapSearch', 'latitude': data.latitude, 'longitude': data.longitude, 'keywords': content})


} else {


this.menusMap = JSON.parse(window.android.isInstallByread())
if (this.menusMap == null || Object.getOwnPropertyNames(this.menusMap).length === 1) {
this.showCancel = false
this.showGaoDei = true
return
}
if (this.menusMap.baiDu === '2') {
this.menusMap.baiDu = '百度地图'
}
if (this.menusMap.gaoDei === '1') {
this.menusMap.gaoDei = '高德地图'
}
if (this.menusMap.tenXun === '3') {
this.menusMap.tenXun = '腾讯地图'
}
this.showMap = true
}
})
},
onCancel () {
},
onConfirmGaoDei () {
window.android.makeUpMap(this.data.latitude, this.data.longitude, '', 0, 2, '')
},
handleClickMenu (value) {
if (value === 'baiDu') {
window.android.makeUpMap(this.data.latitude, this.data.longitude, '', 2, 2, this.text)
} else if (value === 'gaoDei') {
window.android.makeUpMap(this.data.latitude, this.data.longitude, '', 1, 2, this.text)
} else if (value === 'tenXun') {
window.android.makeUpMap(this.data.latitude, this.data.longitude, '', 3, 2, this.text)
}
}
}
}



